<template>
  <div class="image-input" :style="imageStyle" @click="chooseImage">
    <span v-if="!imageData" class="placeholder"> Choose an Image </span>
    <input
      class="file-input"
      ref="fileInput"
      type="file"
      accept="image/*"
      @change="onSelectFile"
    />
  </div>
</template>
  
  <script>
export default {
  name: "InputIcon",
  data() {
    return {
      imageData: null,
    };
  },
  computed: {
    imageStyle() {
      return {
        "background-image": `url(${this.imageData})`,
      };
    },
  },
  methods: {
    chooseImage() {
      this.$refs.fileInput.click();
    },
    onSelectFile() {
      const input = this.$refs.fileInput;
      const files = input.files;
      if (files && files[0] && files[0].type.startsWith("image/")) {
        const reader = new FileReader();
        reader.onload = (e) => {
          this.imageData = e.target.result;
        };
        reader.readAsDataURL(files[0]);
        this.$emit("input", files[0]);
      } else {
        input.value = null;
        this.imageData = null;
        this.$emit("input", null);
      }
    },
  },
};
</script>
  
  <style>
.image-input {
  display: block;
  width: 20rem;
  height: 20rem;
  cursor: pointer;
  background-size: cover;
  background-position: center center;
  border-radius: 10px;
  margin: 0 auto;
}

.placeholder {
  background: var(--bg-input);
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "DotGothic16", sans-serif;
  color: var(--fc-label);
  width: 20rem;
  height: 20rem;
  margin: 0 auto;
  border-radius: 10px;
}

.file-input {
  display: none;
}
</style>
  